
.testdetails_container{

 margin-top: 100px;
 margin-left: 20px;
 margin-right: 20px;
 display: flex;


}

.testdetails_tabs{
    width: 100vw;
}

.tab_container{
    display: flex;
    flex-direction: row;
   
}

.testCard_container{

    margin-top: 20px;
    display: flex;
    width: 30vw;
    z-index: 600;
    }
    
    .testcards{
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        transition: 0.3s;
        width: 100%; 
        margin-left: 10px;
        margin-right: 10px; 
        height: 30vh;
        background-color: white;
    }
    
    .testcards:hover{
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    
    }
    
    .card_top{
        flex: 1;
        display: flex;
        flex-direction: row;
        border-bottom: 2px solid black;
        margin-left: 7px;
        margin-right: 7px;
    }
    
    .cardbtn{
        background-color: #c2ffc2; 
         border: none;
         width: 10vw;
         font-size: bold;
         border-radius: 5px;
         text-align: center;
         text-decoration: none;
         display: inline-block;
         padding: 6px 20px;
         /* margin: 4px 7px; */
         cursor: pointer;
        outline: none;
        color:black;
         transition-duration: 0.4s;
         
    }
    
    .cardbtn1{
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    }
    
    .card_content{
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        margin-left: 10px;
        margin-right: 10px;
    }
    
    .bottom_line{
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:0.7rem;
    color:gray;
    margin-top: 10px;
    }
    
    .taketest_buttn{
       display: flex;
       justify-content: center;
       align-items: center;
       padding-top: 10px;
    }
    



