.businessuser_chat_container{
    margin-top: 70px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 5px;
}

.businessuser_chat_main{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;

}

.businessuser_chat_list_card{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 30%;
    height: 100%;
    overflow: hidden;
    
}

.businessuser_chatcard{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 66%;
    
}

.input_chat_search{
    margin:'10px 10px'
}

.search_head{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.chat_list{
    margin:'1px 8px';
    height:400;
}

.chat_list_card{
    width:100%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}